import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

import Web3 from "web3";
import NftJson from "../abis/NftJson.json";
import NftStakeJson from "../abis/NftStakeJson.json";
import TokenJson from "../abis/TokenJson.json";
import CountDown from '../components/CountDown';

import { useWeb3Context } from "../hooks/web3";


export default function Traning() {

    const { connect, disconnect, connected, providerChainID, checkWrongNetwork, address, provider } = useWeb3Context();


    const [nftContract, setNftContract] = useState(null);
    const [stakingContract, setStakingContract] = useState(null);

    const [mainLoading, setMainLoading] = useState(false);
    const [allNfts, setAllNfts] = useState([]);
    const [stakedNftsId, setStakedNftsId] = useState([])
    const [selectedNftsStake, setSelectedNftsStake] = useState([]);
    const [selectedNftsUnstake, setSelectedNftsUnstake] = useState([]);

    const [stakeBalance, setStakeBalance] = useState('?');
    const [nextReward, setNextReward] = useState('?');
    const [roi, setRoi] = useState("3.36");

    const [claimRwardTime, setClaimRwardTime] = useState(null);
    const [canClaim, setCanClaim] = useState(false);

    const [noticeToShow, setNoticeToShow] = useState("Please connect wallet to Stake/UnStake NFT");

    useEffect(() => {
        if (connected) {
            if (providerChainID == 4) {
                setNoticeToShow('Loading. Please Wait...');
                loadData();
            } else {
                checkWrongNetwork()
            }
        }
    }, [connected]);

    const _connectToAccount = async () => {
        if (!connected) {
            setNoticeToShow('Connecting...');
            connect()
        }
    }

    const loadData = async () => {

        setMainLoading(true);
        const web3 = new Web3(provider);
        window.web3 = web3;
        const networkAddress = '0x358F156001675407C73eB316F468B360dbea7916';
        const _nftContract = await new web3.eth.Contract(NftJson.abi, networkAddress);
        setNftContract(_nftContract);

        const stakeNetworkAddress = '0x7d62156560dBD5777F72A50A15D880B52812d45b';
        const _stakeContract = await new web3.eth.Contract(NftStakeJson.abi, stakeNetworkAddress);
        setStakingContract(_stakeContract);

        // finding all nfts of user
        var _totalNfts = await _nftContract.methods.balanceOf(address).call();
        let _allNftsArray = [];
        let _stakedNftsId = [];
        var _idArray = 0;
        for (let i = 0; i < _totalNfts.toString(); i++) {
            let _nftId = await _nftContract.methods.tokenOfOwnerByIndex(address, i).call();
            let _isStaked = await _stakeContract.methods.getIsNftStakedByUser(_nftId.toString()).call();

            let _nftUri = await _nftContract.methods.tokenURI(_nftId.toString()).call();
            var _jsonUrl = "https://gateway.pinata.cloud/ipfs/" + _nftUri.substring(7);
            var _imageUrl = '';

            await fetch(_jsonUrl)
                .then((response) => response.json())
                .then((responseJson) => {
                    _imageUrl = "https://gateway.pinata.cloud/ipfs/" + responseJson.image.substring(7);
                })

            let _item = {
                _idArray: _idArray,
                id: _nftId.toString(),
                imgUrl: _imageUrl,
                staked: _isStaked,
                selected: false
            }
            _idArray += 1;
            _allNftsArray.push(_item)
            if (_isStaked) {
                _stakedNftsId.push(_nftId.toString());
            }
        }


        // loading all values for user
        const tokenNetworkAddress = '0x883d81b99c7e0703EB35Ee3D8c520d92D7D091b7';
        const _tokenContract = await new window.web3.eth.Contract(TokenJson.abi, tokenNetworkAddress);
        let _balance = await _tokenContract.methods.balanceOf(address).call();
        let _decemals = await _tokenContract.methods.decimals().call();

        let _tokenRewardInOneHour = await _stakeContract.methods.getTokenRewardInOneHour().call();
        var currentTime = Math.floor(Date.now() / 1000);
        let _allStakedNfts = _allNftsArray.filter(item => item.staked === true);

        let _nextReward = 0;

        var _timeArray = [];
        for (let i = 0; i < _allStakedNfts.length; i++) {
            let _nft = _allStakedNfts[i];
            let _nftDetails = await _stakeContract.methods.getNftStakedByUser(_nft.id).call();
            let _staketime = _nftDetails.lastRewardClaimTime.toString();
            _timeArray.push(_staketime);
            let _tokens = (((currentTime - _staketime) / 60) * _tokenRewardInOneHour);
            _nextReward += _tokens;
        }

        setAllNfts(_allNftsArray);
        setSelectedNftsStake([]);
        setSelectedNftsUnstake([]);

        setStakedNftsId(_stakedNftsId);

        setStakeBalance((_balance.toString() / 10 ** _decemals.toString()).toFixed(3));

        setNextReward(_nextReward.toFixed(3));
        setMainLoading(false);

        const _timeForUnstake = await _stakeContract.methods.getMimumTimeForUnstake().call();
        checkForClaim(_timeArray,Number(_timeForUnstake));

        setNoticeToShow('Connected, Now you can Stake/UnStake');
    }

    const checkForClaim = (_timeArray, _timeForUnstake) => {
        if (_timeArray.length > 0) {
            const _max = Math.max(..._timeArray);
            const _currentTime = Math.floor(Date.now() / 1000);
            const _diffrence = _max + _timeForUnstake - _currentTime;

            if (_diffrence > _timeForUnstake) {
                setCanClaim(true);
            } else {
                setClaimRwardTime(_max + _timeForUnstake);
                setCanClaim(false);
            }

        } else {
            setCanClaim(false);
        }
    }

    const forStakeArray = (_id) => {
        var _allnfts = allNfts;
        var _obj = _allnfts[_id];
        var _demoArray = selectedNftsStake;

        if (_obj.selected) {
            _obj.selected = false;
            setSelectedNftsStake(_demoArray.filter(item => item !== _obj.id));
        } else {
            _obj.selected = true;
            setSelectedNftsStake(_demoArray => [..._demoArray, _obj.id]);
        }
        _allnfts[_id] = _obj;
        setAllNfts(_allnfts);
    }

    const forUnStakeArray = (_id) => {
        var _allnfts = allNfts;
        var _obj = _allnfts[_id];
        var _demoArray = selectedNftsUnstake;

        if (_obj.selected) {
            _obj.selected = false;
            setSelectedNftsUnstake(_demoArray.filter(item => item !== _obj.id));
        } else {
            _obj.selected = true;
            setSelectedNftsUnstake(_demoArray => [..._demoArray, _obj.id]);
        }
        _allnfts[_id] = _obj;
        setAllNfts(_allnfts);
    }



    const stakeOne = async (_id) => {

        setNoticeToShow('Staking. Please wait.......');
        try {
            const _data = await stakingContract.methods.stakeNFT(_id).send({ from: address });
            console.log(_data);
            setNoticeToShow('Staked Succefully. Please wait.......');

            loadData();

        } catch (error) {

            console.log("error my friend works");
            console.log(error);
            setNoticeToShow(error.message);
            loadData();
        }

    }

    const unStakeOne = async (_id) => {
        setNoticeToShow('UnStaking. Please wait.......');
        try {
            const _data = await stakingContract.methods.unStakeNFT(_id).send({ from: address });
            console.log(_data);
            setNoticeToShow('Unstaked Succefully. Please wait.......');
            loadData();

        } catch (error) {

            console.log(error);
            setNoticeToShow(error.message);
            loadData();
        }
    }

    const stakeArray = async () => {
        setNoticeToShow('Staking. Please wait.......');
        try {
            const _data = await stakingContract.methods.stakeNFTarray(selectedNftsStake).send({ from: address });
            console.log(_data);
            setNoticeToShow('Staked Succefully. Please wait.......');
            loadData();

        } catch (error) {
            console.log(error);
            setNoticeToShow(error.message);
            loadData();
        }
    }

    const unStakeArray = async () => {
        setNoticeToShow('Unstaking. Please wait.......');

        try {
            const _data = await stakingContract.methods.unStakeNFTarray(selectedNftsUnstake).send({ from: address });
            console.log(_data);
            setNoticeToShow('Unstaked Succefully. Please wait.......');
            loadData();

        } catch (error) {
            console.log(error);
            setNoticeToShow(error.message);
            loadData();
        }
    }

    const claimReward = async () => {

        setNoticeToShow('Claiming Reward. Please wait.......');
            try {
                const _data = await stakingContract.methods.claimRewardarray(stakedNftsId).send({ from: address });
                console.log(_data);
                setNoticeToShow('Reward Claimed. Please wait.......');
                loadData();
    
            } catch (error) {
                console.log(error);
                setNoticeToShow(error.message);
                loadData();
            }
    }

    return (
        <main id="club-main">
            <section id="planmenu" className="sbfc-bg" style={{ backgroundImage: "url(images/hero-bg-1.png)" }}>
                <div className="container">
                    <div className="text-center sbfc-large-icon">
                        <img className="img-fluid" src="images/hero-bg.png" alt="" />
                    </div>
                </div>
            </section>
            <section className="content-thumb-wrapp">
                <div className="container">

                    <div id="salesoon" className="salesoon-bg">

                        {connected
                            ?
                            <div id="mint" className="mint"  >
                                <div className="row align-items-center">
                                    <div className="col-lg-4">
                                        <div className="cw-listing">
                                            <ol>
                                                <li>CONNECT WALLET</li>
                                                <li>SELECT QTY</li>
                                                <li>PRESS "Stake/UnStake"</li>
                                            </ol>
                                        </div>
                                    </div>
                                    <div className="col-lg-4">

                                        <div className="cw-button text-center mb-4" onClick={disconnect}>
                                            <button className="btn btn-outline-dark" >DisConnect</button>
                                        </div>
                                        <div className="text-center">
                                            <p className=" text-dark">{noticeToShow}</p>
                                        </div>
                                    </div>
                                    <div className="col-lg-4">
                                        <div className="cw-copy">
                                            <p><b>Stake and Earn Rewards</b></p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                            :
                            <div id="mint" className="mint"  >
                                <div className="row align-items-center">
                                    <div className="col-lg-4">
                                        <div className="cw-listing">
                                            <ol>
                                                <li>CONNECT WALLET</li>
                                                <li>SELECT QTY</li>
                                                <li>PRESS "Stake/UnStake"</li>
                                            </ol>
                                        </div>
                                    </div>
                                    <div className="col-lg-4">

                                        <div className="cw-button text-center mb-4" onClick={_connectToAccount}>
                                            <button className="btn btn-outline-dark">Connect</button>
                                        </div>
                                        <div className="text-center">
                                            <p className=" text-dark">{noticeToShow}</p>
                                        </div>
                                    </div>
                                    <div className="col-lg-4">
                                        <div className="cw-copy">
                                            <p><b>Stake and Earn Rewards</b></p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        }

                    </div>
                </div>
            </section>

            <section className="nfts-wrapp">
                <div className="container">
                    <div className="nfts-box">
                        <div className="row">
                            <div className="col-xl-6 col-lg-5">
                                <div className="nfts-title">
                                    <h2>MY NFTS</h2>
                                </div>
                                <div className="mt-4 nfts-dolls">
                                    <div className="nfts-lox lox-one"><strong>YOUR STAKE BALANCE</strong> <span><b>{stakeBalance} $LOX</b></span></div>
                                    <div className="nfts-lox lox-two"><strong>Next Reward Amount</strong> <span><b>{nextReward} $LOX</b></span></div>
                                    <div className="nfts-lox lox-three"><strong>ROI (14-Day Rate)</strong> <span><b>{roi} $LOX</b></span></div>
                                    <div className="nfts-lox lox-button">
                                        {connected
                                            ?
                                            claimRwardTime !== null
                                                ?
                                                <button disabled={claimRwardTime - (Date.now() / 1000) > 0} className="btn btn-theme" onClick={claimReward}>Claim
                                                </button>
                                                :
                                                <button disabled={true} className="btn btn-theme" onClick={claimReward}>Claim
                                                </button>
                                            :
                                            <button className="btn btn-theme" onClick={_connectToAccount}>Connect</button>
                                        }
                                    </div>

                                    {claimRwardTime !== null &&
                                        claimRwardTime - (Date.now() / 1000) > 0 &&
                                        <div className="d-flex">
                                            <p className='me-2'>Claim after: </p>
                                            <CountDown countDownDate={claimRwardTime} />
                                        </div>
                                    }
                                    <p className=" text-light">{noticeToShow}</p>
                                </div>
                            </div>
                            <div className="col-xl-6 col-lg-7">
                                <div className="nfts-buttons">
                                    <div className="row">
                                        <div className="col-6">
                                            <button disabled={selectedNftsUnstake.length < 1} className={`btn  ${selectedNftsUnstake.length > 0 ? 'btn-theme' : 'btn-grey'} w-100`} onClick={unStakeArray}>Unstake all {selectedNftsUnstake.length}</button>
                                            <p>{selectedNftsUnstake && selectedNftsUnstake.map((o, key) => {
                                                return (
                                                    <span key={key}>{o},</span>
                                                )
                                            })}
                                            </p>
                                        </div>

                                        <div className="col-6">
                                            <button disabled={selectedNftsStake.length < 1} className={`btn  ${selectedNftsStake.length > 0 ? 'btn-theme' : 'btn-grey'} w-100`} onClick={stakeArray}>Stake all {selectedNftsStake.length}</button>

                                            <p >{selectedNftsStake && selectedNftsStake.map((o, key) => {
                                                return (
                                                    <span key={key}>{o},</span>
                                                )
                                            })}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="nfts-rows">
                            <div className="row">
                                {allNfts != [] &&
                                    allNfts.map((nftObj, key) => {
                                        return (
                                            <div key={key} className="col-lg-3 col-md-6 col-6">
                                                <div className="stake-box">
                                                    <div className="stake-thumb">
                                                        <a className="stake-url" >
                                                            <img className="img-fluid" src={nftObj.imgUrl} alt="" />
                                                        </a>
                                                    </div>
                                                    <div className="stake-title d-flex">
                                                        <h3># {nftObj.id}</h3>
                                                        {nftObj.staked
                                                            ?
                                                            <label className="form-check-label" onClick={() => { forUnStakeArray(nftObj._idArray) }}>
                                                                {nftObj.selected
                                                                    ?
                                                                    <input className="form-check-input" type="checkbox" checked />
                                                                    :
                                                                    <input className="form-check-input" type="checkbox" />
                                                                }
                                                            </label>
                                                            :

                                                            <label className="form-check-label" onClick={() => { forStakeArray(nftObj._idArray) }}>
                                                                {nftObj.selected
                                                                    ?
                                                                    <input className="form-check-input" type="checkbox" checked />
                                                                    :
                                                                    <input className="form-check-input" type="checkbox" />
                                                                }
                                                            </label>
                                                        }
                                                    </div>
                                                    <div className="stake-button">
                                                        {nftObj.staked
                                                            ?
                                                            <button onClick={() => { unStakeOne(nftObj.id) }} className="btn btn-theme" >UNSTAKE</button>
                                                            :
                                                            <button onClick={() => { stakeOne(nftObj.id) }} className="btn btn-theme" >STAKE</button>
                                                        }

                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </main>
    )
}
